/**
 * Execute function at most 1 time per time milliseconds
 * @param {Function} fn
 * @param {number} time
 * @return {function(...[*])}
 */
export default function throttle(fn, time) {
  let isThrottling = false;
  let lastArgs = null;
  return (...args) => {
    lastArgs = args;
    if (!isThrottling) {
      isThrottling = true;
      setTimeout(() => {
        fn(...lastArgs);
        isThrottling = false;
      }, time);
    }
  };
}



