import {svg, path} from 'render/svg';

/**
 *
 * @param {'right'|'left'|'up'|'down'} direction
 * @param {Object<string,*>} attrs
 */
export default function chevron(direction, attrs = {}) {
  const commonAttrs = {
    fill: '#000000',
    viewBox: '0 0 24 24',
    height: '1rem',
    width: '1rem',
    xmlns: 'http://www.w3.org/2000/svg',
  };
  if (direction === 'right') {
    // https://material.io/icons/#ic_chevron_right
    return (
      svg({...commonAttrs, ...attrs}, [
        path({d: 'M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z'}),
        path({d: 'M0 0h24v24H0z', fill: 'none'}),
      ])
    );
  } else if (direction === 'left') {
    // https://material.io/icons/#ic_chevron_left
    return (
      svg({...commonAttrs, ...attrs}, [
        path({d: 'M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z'}),
        path({d: 'M0 0h24v24H0z', fill: 'none'}),
      ])
    );
  } else if (direction === 'up') {
    // https://material.io/icons/#ic_expand_less
    return (
      svg({...commonAttrs, ...attrs}, [
        path({d: 'M12 8l-6 6 1.41 1.41L12 10.83l4.59 4.58L18 14z'}),
        path({d: 'M0 0h24v24H0z', fill: 'none'}),
      ])
    );
  }
  // https://material.io/icons/#ic_expand_more
  return (
    svg({...commonAttrs, ...attrs}, [
      path({d: 'M16.59 8.59L12 13.17 7.41 8.59 6 10l6 6 6-6z'}),
      path({d: 'M0 0h24v24H0z', fill: 'none'}),
    ])
  );
}



