import arraysDiffer from 'utility/arraysDiffer';

/**
 * @param {function} fn function to be memoized
 * @return {function(...[*]=)}
 */
export default function memoizeComponent(fn) {
  let lastArgs;
  let lastReturn;
  return (...args) => {
    if (arraysDiffer(lastArgs, args)) {
      lastArgs = args;
      lastReturn = fn(...args);
    }
    return lastReturn;
  };
}



