import each from './each';

/**
 * @param {Array|Object|*} collection
 * @param {Function|undefined} valFn
 * @return {number}
 */
export default function sum(collection, valFn = i => i) {
  let total = 0;
  each(collection, v => total += valFn(v));
  return total;
}



