import english from './languages/english';

let language = english;

// Not used, but here's how we could set the language dynamically
// Also would want to load these async if we supported many langs
// or had a large number of phrases
export const setLang = (lang) => {
  language = lang;
};

/**
 * Lookup function to get string for message id.
 *   If the message is a function, it'll call the function with the provided
 *   arguments.
 *     eg message('items.cartSize', 5) => 'You have 5 items in your cart'
 * @param {string} id - phrase identifier
 * @param {*} args - arguments passed to message function if it is a function
 * @return {string} message - formatted message result
 */
export default function message(id, ...args) {
  let msg = english[id];
  if (typeof msg === 'function') {
    msg = msg(...args);
  }
  return msg;
}



