/**
 * When images load, check to see if all images in row have loaded.
 *   if all have loaded, set data-children-loaded to true on parent element
 *   so that we can begin the css fade in transition.
 * Note: if we were using react or a more mature framework than the one I wrote
 *   we would want to store this in the framework and not on the DOM. The issue
 *   here is that I have not implemented partial tree updates and triggering a
 *   full rerender is somewhat expensive
 * @param {Event} e
 */
export default function handleImageLoad(e) {
  const parent = e.target.parentElement;
  const collectionEl = parent.parentElement;
  parent.setAttribute('data-loaded', 'true');
  const children = collectionEl.childNodes ? [...collectionEl.childNodes] : [];
  if (children.find(el => !el.hasAttribute('data-loaded'))) {
    return;
  }
  collectionEl.setAttribute('data-children-loaded', 'true');
}



