import {div} from 'render/html';
import style, {styleSelector} from 'style';
import {
  topRight, absolute, square, transitionColor,
  fill, visible, clickable, p,
} from 'style/atomic';
import {WHITE, DARK_GRAY} from 'style/colors';
import {close} from '../icons';

/**
 * Close button (X icon) on the top right of the lightbox backdrop
 * @param {Function} onClick
 */
export default function closeButton(onClick) {
  return (
    div(
      {...style(topRight, p(3), absolute), ...styleSelector(':hover > svg', fill(WHITE))},
      close({
        onClick,
        ...style(fill(DARK_GRAY), visible(), square('3vw'), transitionColor, clickable),
      }),
    )
  );
}



