/**
 * Returns true if arrays a or be are undefined, if their lengths are different
 *   or if they contain different entries
 * @param {Array} a
 * @param {Array} b
 * @return {boolean}
 */
export default function arraysDiffer(a, b) {
  if (a === undefined || b === undefined) return true;
  if (a.length !== b.length) return true;
  for (let i = 0; i < a.length; i++) {
    if (a[i] !== b[i]) return true;
  }
  return false;
}



