let el;

/**
 * Add style rule to document's CSS
 * @param {string} styleRule
 */
export default function addRule(styleRule) {
  if (!el) {
    el = document.createElement('style');
    document.head.appendChild(el);
  }
  el.appendChild(document.createTextNode(styleRule));
}



