/**
 * Execute function at most once per animation frame
 *   Also acts to defer execution until after current execution is finished.
 * @param {Function} fn
 * @return {function(...[*])}
 */
export default function throttleAnimationFrame(fn) {
  let lastArgs;
  let hasRequested = false;
  return (...args) => {
    lastArgs = args;
    if (!hasRequested) {
      window.requestAnimationFrame(() => {
        hasRequested = false;
        fn(...lastArgs);
      });
    }
  };
}



