import {toList} from 'utility/each';

export default class ImmutableModel {
  constructor() {
    this.__data = {};
  }
  set(key, value) {
    let result = this;
    if (result.__data[key] !== value) {
      result = result.fork();
      result.__data = {...result.__data, [key]: value};
    }
    return result;
  }
  entries() {
    return toList(this.__data);
  }
  update(key, updater) {
    const newVal = updater(this.get(key));
    return this.set(key, newVal);
  }
  get(key, defaultValue) {
    return this.__data.hasOwnProperty(key) ? this.__data[key] : defaultValue;
  }
  fork() {
    const forked = Object.create(this.constructor.prototype);
    forked.__data = this.__data;
    return forked;
  }
}



