import {div} from 'render/html';
import style from 'style';
import {
  absolute, fullView, bgTransparentGray, topLeft,
} from 'style/atomic';


/**
 * Lightbox transparent backdrop
 * @param {Function} onClick
 */
export default function lightboxBackdrop(onClick) {
  return (
    div({
      ...style(absolute, fullView, bgTransparentGray, topLeft),
      onClick,
    })
  );
}



