import each from './each';

/**
 * Group items into groups of size groupSize.
 *   if not evenly divisible, will add to last member of list
 * @param collection
 * @param {number} groupSize
 * @return {Array[]}
 */
export default function groups(collection, groupSize) {
  const out = [];
  let last;
  each(collection, (v, index) => {
    const newGroup = (index % groupSize) === 0;
    if (newGroup) {
      last = [];
      out.push(last);
    }
    last.push(v);
  });
  return out;
}



