import callbackAttributes from './callbackAttributes';

/**
 * update DOM to reflect new virtual element dom structure
 * @param {HTMLElement} el
 * @param {VirtualElement|null} oldElement
 * @param {VirtualElement} newElement
 */
export default function update(el, oldElement, newElement) {
  // short circuit if memoized
  if (oldElement === newElement) return;

  // Update attributes on element
  newElement.removedAttributes(oldElement).forEach(([val, attr]) => {
    if (callbackAttributes.has(attr)) {
      el.removeEventListener(attr, callbackAttributes.get(attr));
    } else {
      el.removeAttribute(attr);
    }
  });
  newElement.updatedAttributes(oldElement).forEach(([val, attr]) => {
    if (callbackAttributes.has(attr)) {
      const eventName = callbackAttributes.get(attr);
      if (oldElement !== null && oldElement.hasAttribute(attr)) {
        el.removeEventListener(eventName, oldElement.getAttribute(attr));
      }
      el.addEventListener(eventName, val);
    } else {
      el.setAttribute(attr, val);
    }
  });

  if (newElement.text !== null) {
    if (oldElement === null || oldElement.text !== newElement.text) {
      el.innerHTML = newElement.text;
    }
    return;
  }

  // Make lookup of keys => children[HTMLElement, VirtualElement]
  const lookup = oldElement === null ? new Map() : oldElement.makeElementLookup(el);
  newElement.removeChildKeys(oldElement).forEach(key => {
    const [childEl] = lookup.get(key);
    el.removeChild(childEl);
    oldElement.children.get(key).ref(null);
  });
  let nextChild = el.firstChild;
  newElement.children.entries().forEach(([child, key]) => {
    // Create if new element
    if (oldElement === null || !oldElement.children.has(key)) {
      const newEl = document.createElementNS(child.namespace, child.tag);
      update(newEl, null, child);
      el.insertBefore(newEl, nextChild);
      child.ref(newEl);
    } else {
      const [oldEl, oldVirtualEl] = lookup.get(key);
      // shuffle to new position if needed
      if (nextChild !== oldEl) {
        el.removeChild(oldEl);
        el.insertBefore(oldEl, nextChild);
      }
      nextChild = oldEl.nextSibling;
      update(oldEl, oldVirtualEl, child);
    }
  });

  // clean up any dangling children (shouldn't be any)
  while (nextChild) {
    const oldNextChild = nextChild;
    nextChild = oldNextChild.nextSibling;
    oldNextChild.parentElement.removeChild(oldNextChild);
  }
}



