import {div, img} from 'render/html';
import style from 'style';
import {
  absolute, visible, relative, bottomLeft, alignMiddle, textBackground,
  p, parentWidth, boxShadow,
} from 'style/atomic';

/**
 * Image display of lightbox- displays image with title overlay
 * @param {string} imageUrl
 * @param {string} imageTitle
 */
export default function imageDisplay(imageUrl, imageTitle) {
  return (
    div(style(relative, visible(), boxShadow), [
      img({src: imageUrl, ...style(alignMiddle, visible(), {'max-width': '80vw', 'max-height': '80vh'})}),
      div(style(parentWidth, bottomLeft, absolute, textBackground, p(2)), imageTitle),
    ])
  );
}



