import 'babel-polyfill';

import update from 'render/update';
import {onStateChange, createStore, getState} from 'data-store';
import throttleAnimationFrame from 'utility/throttleAnimationFrame';

import appView from './app.view';
import AppStore from './App.store';

const el = document.createElement('div');
document.body.appendChild(el);
let lastDOM = null;
let renderView = appView;

onStateChange(throttleAnimationFrame((state) => {
  const newDOM = renderView(state);
  update(el, lastDOM, newDOM);
  lastDOM = newDOM;
}));

createStore(new AppStore());

if (module.hot) {
  module.hot.accept('./app.view', () => {
    renderView = require('./app.view').default;
    const newDOM = renderView(getState());
    update(el, lastDOM, newDOM);
    lastDOM = newDOM;
  });
}



