import {div, b} from 'render/html';
import totoro from 'components/images/totoro';
import style from 'style';
import {mt} from 'style/atomic';
import {hideLoginGuideMessage} from 'app/app.actions';
import {message} from 'i18n';

import {loginGuideStyle, speechBubbleStyle} from './login.style';



/**
 * Login guide that pops up and tells user information
 * @param hideMessage
 */
export default function loginGuide(hideMessage) {
  return (
    div(loginGuideStyle, [
      totoro(),
      !hideMessage && div({...speechBubbleStyle, onClick: hideLoginGuideMessage}, [
        b({}, message('guide.message.title')),
        div(style(mt(1)), message('guide.message.body')),
      ]),
    ])
  );
}



