import {div} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import style from 'style';
import {
  absolute, parentWidth, p, textWhite, clickable,
  textGradient, bottomLeft,
} from 'style/atomic';

/**
 * Image title information displayed at bottom of image tile
 * @param {string} imageTitle
 */
function imageInfoView(imageTitle) {
  const styles = style(
    absolute,
    parentWidth,
    p(2),
    textWhite,
    clickable,
    bottomLeft,
    textGradient,
  );
  return (
    div({class: 'details', ...styles}, [
      imageTitle,
    ])
  );
}

export default memoizeComponent(imageInfoView);



