import {div} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {
  parentWidth, p3, borderY, scrollHorizontal,
} from 'style/atomic';
import style from 'style';

import categorySelectionMode from './categorySelectionMode.view';
import searchMode from './searchMode.view';

/**
 * @param {string} activeCategory
 * @param {string[]} photoCategories
 * @param {boolean} loggedIn
 * @param {boolean} isSearchMode
 */
function categoriesView(activeCategory, photoCategories, loggedIn, isSearchMode) {
  return (
    div({...style(parentWidth, p3, borderY, !isSearchMode && scrollHorizontal), id: 'categories-header'}, [
      !isSearchMode && categorySelectionMode(loggedIn, activeCategory, photoCategories),
      isSearchMode && searchMode(),
    ])
  );
}

export default memoizeComponent(categoriesView);




