import {div, span, b} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {
  flexGrow, scrollVertical, bgLightGray, relative, textCenter, p,
} from 'style/atomic';
import style from 'style';
import {isSize, MOBILE} from 'style/sizes';
import {
  loadImages, goToNextFullScreenImage,
  goToPreviousFullScreenImage, closeFullScreenImage,
} from 'app/app.actions';
import groups from 'utility/groups';
import lightbox from 'components/lightbox';
import {message} from 'i18n';
import imageGroupView from './imageGroup.view';
import imageLoadingView from './imageLoading.view';

let scrollContainer;

const onScroll = () => {
  if (scrollContainer) {
    const {clientHeight, scrollTop, scrollHeight} = scrollContainer;
    const renderHeight = clientHeight * 3;
    const renderBottom = scrollTop + renderHeight;
    if (renderBottom > scrollHeight) {
      loadImages();
    }
  }
};

const scrollRef = (el) => {
  scrollContainer = el;
  onScroll();
};

/**
 * View of images including tiling, lightbox, and loading state feedback
 * @param {Image[]} images
 * @param {Image|undefined} currentFullScreenImage
 * @param {boolean} isLoading
 * @param {number} totalResults
 * @param {string} term
 * @param {string} loadErrorMessage
 */
function imageView(images, currentFullScreenImage, isLoading, totalResults,
                   term, loadErrorMessage) {
  const GROUP_SIZE = 5;
  if (images.length === 0) loadImages();
  const imgGroups = groups(images, GROUP_SIZE);
  const noResults = totalResults === 0;

  return (
    div(
      {
        onScroll,
        ...style(flexGrow(), scrollVertical, bgLightGray, relative),
      },
      [
        noResults && div(style(textCenter, p(5)), [
          span({}, message('images.status.noneFound')),
          b({}, `"${term}"`),
        ]),
        loadErrorMessage && div(style(textCenter, p(5)), loadErrorMessage),
        ...imgGroups.map((imgGroup, idx) =>
          imageGroupView(imgGroup, idx, idx * GROUP_SIZE)),
        currentFullScreenImage && !isSize(MOBILE) && lightbox(
          currentFullScreenImage.largeUrl,
          currentFullScreenImage.title,
          goToNextFullScreenImage,
          goToPreviousFullScreenImage,
          closeFullScreenImage,
        ),
        isLoading && imageLoadingView(),
      ],
      undefined,
      scrollRef,
    )
  );
}

export default memoizeComponent(imageView);



