import {div, h3} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {
  flexHorizontal, clickable, mx, flexCenterContent,
} from 'style/atomic';
import style from 'style';
import {selectCategory, enterSearchMode} from 'app/app.actions';
import {search} from 'components/icons';
import {
  BLACK, MEDIUM_GRAY,
} from 'style/colors';
import {wrapperStyle, categoryTitle} from './category.style';

/**
 * Header with selectable categories and search trigger button
 * @param {boolean} loggedIn
 * @param {string} activeCategory
 * @param {string[]} categories
 */
function categorySelectionMode(loggedIn, activeCategory, categories) {
  return (
    div(wrapperStyle(false), [
      div(
        style(flexHorizontal, flexCenterContent),
        search({
          alt: 'Search for images',
          onClick() {
            if (loggedIn) enterSearchMode();
          },
          ...style({fill: loggedIn ? BLACK : MEDIUM_GRAY}, loggedIn && clickable, mx(1)),
        }),
      ),
      ...categories.map(category => (
        h3(
          {
            ...categoryTitle(loggedIn, category, activeCategory),
            onClick() {
              if (loggedIn) selectCategory(category);
            },
          },
          category,
          category,
        )
      )),
    ])
  );
}

export default memoizeComponent(categorySelectionMode);



