let fn;
export default function registerDirectionalKeyCallbacks(el, onGoToNext, onGoToPrevious) {
  if (el) {
    if (fn) document.removeEventListener('keyup', fn);
    fn = function callback(e) {
      if (e.key === 'ArrowLeft') onGoToPrevious();
      if (e.key === 'ArrowRight') onGoToNext();
    };
    document.addEventListener('keyup', fn);
  } else {
    document.removeEventListener('keyup', fn);
    fn = undefined;
  }
}



