import {div} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import style from 'style';
import {
  absolute, fullView, flexCenterContent, topLeft, flexVertical,
  visible, fixed,
} from 'style/atomic';

import lightboxBackdrop from './lightboxBackdrop';
import sideDirectionalNavigation from './sideDirectionalNavigation';
import registerDirectionalKeyCallbacks from './registerDirectionalKeyCallbacks';
import closeButton from './closeButton';
import imageDisplay from './imageDisplay';

/**
 * Lightbox image display (modal with full screen image)
 * @param {string} imageUrl - url of image to display
 * @param {string} imageTitle - title of imate
 * @param {Function} onGoToNext - callback called when previous is clicked
 * @param {Function} onGoToPrevious - callback called when next is clicked
 * @param {Function} onClose - callback called when lightbox is closed
 */
function lightbox(imageUrl, imageTitle, onGoToNext, onGoToPrevious, onClose) {
  const full = {
    ...absolute, ...fullView, ...topLeft, ...fixed,
  };
  return (
    div(
      style(full),
      [
        lightboxBackdrop(onClose),
        closeButton(onClose),
        div(style(full, flexCenterContent, flexVertical, visible(false)), [
          sideDirectionalNavigation('left', onGoToPrevious),
          imageDisplay(imageUrl, imageTitle),
          sideDirectionalNavigation('right', onGoToNext),
        ]),
      ],
      null,
      el => registerDirectionalKeyCallbacks(el, onGoToNext, onGoToPrevious),
    )
  );
}

export default memoizeComponent(lightbox);



