import {toList} from 'utility/each';
import OrderedMap from 'utility/OrderedMap';

export default class VirtualElement {
  /**
   * @param {string} tag - html tag of element (ie div => <div>...</div>)
   * @param {Object.<string, string>} attributes - object with keys as html
   *   attribute key, and and value as the value. eg. ({class: 'p1'}) => <div class="p1">...
   *   Event callbacks are automatically wired to components, so ({onClick=myCallback}) will add
   *   onclick to the element.
   * @param {string|null} key - unique identifier of element, used to determine which node
   *   an element should be compared to when comparing virtual trees to determine how
   *   to update
   * @param {VirtualElement[]} children - list of children virtual elements
   * @param {Function} ref - callback called with element when component mounts
   * @param {string} namespace - see https://www.w3schools.com/xml/xml_namespaces.asp
   */
  constructor(tag, attributes = {}, key = null, children = [], ref = () => null, namespace = null) {
    this.tag = tag;
    this.text = null;
    this.children = new OrderedMap();
    this.processChildren(children);
    this.attributes = attributes;
    this.key = key;
    this.ref = ref;
    this.namespace = namespace;
  }

  /**
   * Return list of attributes that should be removed from old
   * element to match new element
   * @param {VirtualElement|null} old
   * @return {[*, string]}
   */
  removedAttributes(old) {
    const out = [];
    if (old === null) return out;

    toList(old.attributes).forEach(([value, attr]) => {
      if (!this.hasAttribute(attr)) {
        out.push([value, attr]);
      }
    });
    return out;
  }

  /**
   * Return list of attributes that should be updated or added
   * from old element to match new element
   * @param {VirtualElement|null} old
   * @return {[*, string]}
   */
  updatedAttributes(old) {
    const out = [];
    toList(this.attributes).forEach(([value, attr]) => {
      if (old === null || old.attributes[attr] !== value) {
        out.push([value, attr]);
      }
    });
    return out;
  }

  /**
   * Return list of children that should be removed from old
   * element to match new element
   * @param {VirtualElement|null} old
   * @return {string[]} keys
   */
  removeChildKeys(old) {
    const out = [];
    if (old === null) return out;
    old.children.keys().forEach(key => {
      if (!this.children.has(key)) {
        out.push(key);
      }
    });
    return out;
  }

  /**
   * Check if element has attribute
   * @param {string} attr
   * @return {boolean}
   */
  hasAttribute(attr) {
    return this.attributes.hasOwnProperty(attr);
  }

  /**
   * Get attribute for element
   * @param {string} attr - attribute key
   * @return {*}
   */
  getAttribute(attr) {
    return this.attributes[attr];
  }

  /**
   * Add children to element's representation of its children
   * @param {string|VirtualElement[]} children
   */
  processChildren(children) {
    if (children.length === 1 && typeof children[0] === 'string') {
      this.text = children[0];
    } else {
      children.forEach((child, i) => {
        if (!child) return;
        const key = child.key || `${child.tag}:${i}`;
        this.children.set(key, child);
      });
    }
  }

  /**
   * Make lookup of children keys to their VirtualElement and HTMLElement
   * representations so we can query for specific html elements and their
   * virtual counterparts together.
   * @param {HTMLElement} el
   * @return {Map<string,[HTMLElement,VirtualElement]>}
   */
  makeElementLookup(el) {
    const children = el.childNodes ? [...el.childNodes] : [];
    const lookup = new Map();
    this.children.entries().forEach(([child, key, i]) => {
      lookup.set(key, [children[i], child]);
    });
    return lookup;
  }
}



