import ElementType from './VirtualElement.model';

/**
 * Creates element factory function that returns element with specified tag
 *   and namespace. Please see ./html.js and ./svg.js for wrapped html and svg
 *   elements
 * @param {string} tag
 * @param {string} namespace
 * @return {function} Element factory
 */
export default function element(tag, namespace) {
  return (attrs, children = [], key, ref) => {
    const childrenArr = Array.isArray(children) ? children : [children];
    return new ElementType(tag, attrs, key, childrenArr, ref, namespace);
  };
}



