export const MOBILE = [0, 500];
export const SMALL = [501, 850];
export const MEDIUM = [851, 1100];
export const LARGE = [1101, parseFloat('Infinity')];

/**
 * Return current window size range
 * @return {MOBILE|SMALL|MEDIUM|LARGE}
 */
export function getWindowSize() {
  const w = window.innerWidth;
  for (const range of [MOBILE, SMALL, MEDIUM, LARGE]) {
    const [low, high] = range;
    if (low < w && high > w) return range;
  }
}

/**
 * Check if current window size is within a size range
 * @param {MOBILE|SMALL|MEDIUM|LARGE} size
 * @return {boolean}
 */
export const isSize = size => getWindowSize() === size;



