import {
  flexHorizontal, flexSpaceBetween, my, mx, bgWhite, textUpper, clickable,
  textMediumGray, underline, transitionColor, px, flexAlignItems,
} from 'style/atomic';
import style, {sizeStyle} from 'style';
import {SMALL, MEDIUM, LARGE} from 'style/sizes';

/**
 * @param {boolean} isSearch
 */
export const wrapperStyle = isSearch => ({
  ...style(flexHorizontal, !isSearch && flexSpaceBetween, flexAlignItems),
  ...sizeStyle([LARGE, null], px(6)),
  ...sizeStyle(MEDIUM, px(4)),
  ...sizeStyle(SMALL, px(2)),
});

/**
 * @param {boolean} loggedIn
 * @param {string} category
 * @param {string} activeCategory
 */
export const categoryTitle = (loggedIn, category, activeCategory) => ({
  ...style(
    bgWhite,
    my(0),
    mx(1),
    textUpper,
    loggedIn && activeCategory === category && underline,
    loggedIn && clickable,
    !loggedIn && textMediumGray,
    transitionColor,
  ),
});



