import {div, h3} from 'render/html';
import totoro from 'components/images/totoro';
import style, {addRule} from 'style';
import {
  parentWidth, parentHeight, flexCenterContent, flexVertical, absolute,
  textMediumGray, mt, animate, opacity,
} from 'style/atomic';
import {message} from 'i18n';

addRule(`
@keyframes totoro-animate {
  0% {
    transform: rotate(-10deg) translate(-12px, 0);
  }
  50% {
    transform: rotate(10deg) translate(12px, 0);
  }
  100% {
    transform: rotate(-10deg) translate(-12px, 0);
  }
}
`);

/**
 * Loading "spinner" (totoro dancing:))
 */
export default function imageLoadingView() {
  return (
    div(style(parentWidth, parentHeight, flexCenterContent, flexVertical, absolute), [
      totoro(style(animate({name: 'totoro-animate'}), opacity(0.4))),
      h3(style(textMediumGray, mt(4)), message('images.status.loading')),
    ])
  );
}



