import {div, img} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {setFullScreenImage} from 'app/app.actions';

import imageInfoView from './imageInfo.view';
import {wrapperStyle, imageStyle, container, getWidthGetter} from './imageGroup.style';
import handleImageLoad from './handleImageLoad';

/**
 * Image row (group) displayed with dynamic tiling
 * @param {Image[]} images
 * @param {number} groupIndex
 * @param {number} offset
 */
function imgGroupView(images, groupIndex, offset) {
  const getWidth = getWidthGetter(images);
  return div(
    wrapperStyle(groupIndex),
    images.map((imgModel, i) =>
      div(
        {
          onClick: () => setFullScreenImage(i + offset),
          ...container(getWidth(i), i),
        },
        [
          img({
            src: imgModel.previewUrl,
            onLoad: handleImageLoad,
            ...imageStyle,
          }),
          imageInfoView(imgModel.title),
        ],
        imgModel.id,
      )),
    `imgs:${offset}`,
    el => el,
  );
}

export default memoizeComponent(imgGroupView);



