import {ImmutableStore} from 'data-store';
import ImageModel from 'images/Image.model';
import get from 'http/get';

import {
  LOGIN, IMAGES_LOADED, SELECT_CATEGORY, IMAGE_LOAD_STARTED, SET_CURRENT_FULLSCREEN_IMAGE,
  CLOSE_FULL_SCREEN_IMAGE, ENTER_SEARCH_MODE, EXIT_SEARCH_MODE,
  GO_TO_NEXT_FULLSCREEN_IMAGE, IMAGES_LOAD_FAILED, SHOW_LOGIN_GUIDE,
  GO_TO_PREVIOUS_FULLSCREEN_IMAGE, UPDATE_SEARCH_TERM, HIDE_LOGIN_GUIDE_MESSAGE,
} from './app.actions';

export default class AppStore extends ImmutableStore {
  get hideLoginGuideMessage() { return this.get('hideLoginGuideMessage'); }
  get showLoginGuide() { return this.get('showLoginGuide'); }
  get loadErrorMessage() { return this.get('loadErrorMessage'); }
  get isLoading() { return this.get('isLoading'); }
  get isLoggedIn() { return this.get('isLoggedIn'); }
  get isSearchMode() { return this.get('isSearchMode'); }
  get searchTerm() { return this.get('searchTerm'); }
  get currentCategory() { return this.get('currentCategory'); }
  get totalResults() { return this.get('totalResults'); }
  /** @return {string[]} */
  get photoCategories() { return this.get('photoCategories', []); }
  /** @return {Image[]} */
  get images() { return this.get('images', []); }
  get currentFullScreenImage() { return this.images[this.get('currentFullScreenImageIndex')]; }
  constructor() {
    super();
    const photoCategories = [
      'Fog', 'Celebrity', 'Dogs', 'Design',
    ];
    return this
      .set('hideLoginGuideMessage', false)
      .set('showLoginGuide', false)
      .set('photoCategories', photoCategories)
      .set('currentCategory', photoCategories[0])
      .set('totalResults', null)
      .set('page', 1)
      .set('pageSize', 50)
      .set('images', [])
      .set('isLoading', false)
      .set('isLoggedIn', false)
      .set('isSearchMode', false)
      .set('searchTerm', null)
      .set('currentFullScreenImageIndex', -1);
  }
  [HIDE_LOGIN_GUIDE_MESSAGE]() {
    return this.set('hideLoginGuideMessage', true);
  }
  [SHOW_LOGIN_GUIDE]() {
    return this.set('showLoginGuide', true);
  }
  [ENTER_SEARCH_MODE]() {
    return this
      .set('isSearchMode', true)
      .resetSearchData();
  }
  [EXIT_SEARCH_MODE]() {
    return this
      .set('isSearchMode', false)
      .resetSearchData();
  }
  /** @param {string} term */
  [UPDATE_SEARCH_TERM](term) {
    if (term === this.searchTerm) return this;
    return this
      .set('searchTerm', term)
      .resetSearchData();
  }
  [LOGIN]() {
    return this.set('isLoggedIn', true);
  }
  [IMAGE_LOAD_STARTED]() {
    return this
      .set('isLoading', true);
  }

  /**
   * @param {number} countImages
   * @param {Image[]} images
   */
  [IMAGES_LOADED](countImages, images) {
    if (!this.isLoading) return this;
    return this
      .set('totalResults', countImages)
      .set('isLoading', false)
      .update('images', oldImg => [...oldImg, ...images])
      .update('page', p => p + 1);
  }
  /** @param {string} msg */
  [IMAGES_LOAD_FAILED](msg) {
    if (!this.isLoading) return this;
    return this.set('loadErrorMessage', msg);
  }
  /** @param {string} category */
  [SELECT_CATEGORY](category) {
    return this
      .set('currentCategory', category)
      .resetSearchData();
  }
  /** @param {number} imageIndex */
  [SET_CURRENT_FULLSCREEN_IMAGE](imageIndex) {
    return this.set('currentFullScreenImageIndex', imageIndex);
  }
  [GO_TO_NEXT_FULLSCREEN_IMAGE]() {
    return this.moveFullScreenImage(1);
  }
  [GO_TO_PREVIOUS_FULLSCREEN_IMAGE]() {
    return this.moveFullScreenImage(-1);
  }
  [CLOSE_FULL_SCREEN_IMAGE]() {
    return this.set('currentFullScreenImageIndex', -1);
  }
  resetSearchData() {
    return this
      .set('page', 1)
      .set('isLoading', false)
      .set('images', [])
      .set('currentFullScreenImageIndex', -1)
      .set('totalResults', null);
  }

  /**
   * @param {number} delta
   * @return {AppStore}
   */
  moveFullScreenImage(delta) {
    const currImageIndex = this.get('currentFullScreenImageIndex');
    let candidateIndex = currImageIndex + delta;
    if (candidateIndex < 0) {
      candidateIndex = (this.images.length - (Math.abs(candidateIndex) % this.images.length));
    }
    return this.set('currentFullScreenImageIndex', candidateIndex);
  }
  shouldLoadImages() {
    return !this.__hasLoadedAllImages() && !this.isLoading && this.getQueryTerm() !== '';
  }
  __hasLoadedAllImages() {
    return this.totalResults !== null && this.totalResults === this.images.length;
  }
  getQueryTerm() {
    return this.isSearchMode ? this.searchTerm : this.currentCategory;
  }
  loadImages() {
    const term = this.getQueryTerm();
    const page = this.get('page');
    let pageSize = this.get('pageSize');
    const totalResults = this.get('totalResults');
    if (totalResults !== null) {
      const nextTotal = (page + 1) * pageSize;
      if (totalResults < nextTotal) {
        pageSize = Math.max(0, totalResults - this.images.length);
      }
      if (pageSize === 0) {
        return Promise.resolve([totalResults, []]);
      }
    }
    return get(
      'https://api.gettyimages.com/v3/search/images?' +
      'fields=id,title,preview,comp,referral_destinations,max_dimensions' +
      `&sort_order=best&phrase=${term}&page=${page}&page_size=${pageSize}`,
      {'Api-Key': 'h56tksfcqkha9bj36v5unvwt'},
    )
      .then(({result_count: count, images}) => {
        return [count, images.map(img => ImageModel.fromGettyBackend(img))];
      });
  }
}



