import each from 'utility/each';

/**
 * Serialize css key,value object to css style string
 *   eg: {color: 'red', width: '5px'} => 'color:red;width:5px;'
 * @param obj
 * @return {string}
 */
export default function objToString(obj) {
  let outputStr = '';
  each(obj, (value, property) => {
    outputStr += `${property}:${value};`;
  });
  return outputStr;
}



