/**
 * Create function that is only callable once
 * @param fn
 * @return {function(...[*])}
 */
export default function once(fn) {
  let wasCalled = false;
  return (...args) => {
    if (wasCalled) return;
    wasCalled = true;
    fn(...args);
  };
}



