import OrderedMapNode from './OrderedMapNode';

export default class OrderedMap {
  /** @return {number} */
  get size() { return this.__map.size; }
  constructor(...args) {
    this.clear();
    if (args.length) {
      throw new Error('OrderedMap does not support initialization with iterable');
    }
  }
  clear() {
    this.__map = new Map();
    this.__first = new OrderedMapNode(null, null);
    this.__last = new OrderedMapNode(null, null, this.__first);
    this.__first.next = this.__last;
  }
  ['delete'](key) {
    const node = this.__map.get(key);
    if (node) {
      // Note that since we use dummy head/tails, we never need to check
      // if our node is the first or last
      node.remove();
      this.__map.delete(key);
      return true;
    }
    return false;
  }
  entries() {
    let i = 0;
    let out = [];
    for (let node = this.__first.next; node !== this.__last; node = node.next) {
      out.push([node.value, node.key, i++]);
    }
    return out;
  }
  keys() {
    return this.entries().map(([val, key]) => key);
  }
  values() {
    return this.entries().map(([val, key]) => val);
  }
  /**
   * @param {string} key
   * @return {boolean} true if value in OrderdMap
   */
  has(key) {
    return this.__map.has(key);
  }
  /**
   * @param {string} key
   * @param {*} defaultValue
   * @return {*} value if has value, else defaultValue
   */
  get(key, defaultValue) {
    if (this.has(key)) {
      return this.__map.get(key).value;
    }
    return defaultValue;
  }
  /**
   * @param {string} key
   * @param {*} value
   * @return {OrderedMap}
   */
  set(key, value) {
    if (this.has(key)) {
      this.__map.get(key).value = value;
    } else {
      const node = new OrderedMapNode(key, value);
      this.__map.set(key, node);
      this.__last.insertBefore(node);
    }
    return this;
  }
}



