import {div, input} from 'render/html';
import {inputStyle, focused} from 'components/labeledInput';
import memoizeComponent from 'render/memoizeComponent';
import {flexGrow, mx, py, fontSize, clickable} from 'style/atomic';
import {LARGE} from 'style/fonts';
import style, {focusStyle} from 'style';
import {updateSearchTerm, exitSearchMode} from 'app/app.actions';
import {search, close} from 'components/icons';
import throttle from 'utility/throttle';

import {wrapperStyle} from './category.style';

const handleKeyUp = throttle((e) => {
  updateSearchTerm(e.target.value);
}, 1000);

/**
 * Search mode view- updates store with search term when user enters text (throttled)
 */
function searchModeView() {
  return (
    div(wrapperStyle(false), [
      search(style(mx(1))),
      input({
        autofocus: true,
        placeholder: 'Search for Food, Fashion, Yorkies',
        ...style(flexGrow(), inputStyle, mx(4), fontSize(LARGE), py(0)),
        ...focusStyle(focused),
        onKeyUp: handleKeyUp,
      }),
      close({onClick: exitSearchMode, ...style(clickable)}),
    ])
  );
}

export default memoizeComponent(searchModeView);



