import {div} from 'render/html';
import {relative, fillContainer} from 'style/atomic';
import style from 'style';

// https://medium.com/@AmJustSam/how-to-do-css-only-frosted-glass-effect-e2666bafab91
const blur = style(fillContainer, {
  content: '',
  background: 'inherit',
  top: '-25px',
  left: '-25px',
  width: '200%',
  height: '200%',
  'box-shadow': 'inset 0 0 0 3000px rgba(255,255,255,0.3)',
  filter: 'blur(10px)',
});

/**
 * Blurred (frosted glass effect) tile, requires width and height to
 *   be set explicitly
 * @param {Object<string,*>} attrs
 * @param {VirtualElement|VirtualElement[]|string|boolean} children
 */
export default function blurred(attrs, children) {
  return (
    div({...style(relative, {background: 'inherit'}), ...attrs}, [
      div(blur),
      div(style(fillContainer), children),
    ])
  );
}



