import {div} from 'render/html';
import {
  fillContainer, flexHorizontal, flexCenterContent,
} from 'style/atomic';
import style from 'style';

/**
 * Center content
 * @param {Object<string,*>} attrs
 * @param {VirtualElement|VirtualElement[]|string|boolean} children
 */
export default function centered(attrs, children) {
  return (
    div(
      {...style(fillContainer, flexHorizontal, flexCenterContent), ...attrs},
      children,
    )
  );
}



