import style, {styleSelector} from 'style';
import {
  px, py, mt, background, borderRadius, animate, zIndex, opacity, absolute, clickable,
} from 'style/atomic';
import {WHITE} from 'style/colors';
import addRule from 'style/addRule';

addRule(`
@keyframes totoro-guide-animate {
  0% {
    top: 100%;
  }
  100% {
    top: calc(100% - 81px);
  }
}

@keyframes totoro-guide-shake {
  0% {
    transform: rotate(-10deg) translate(-12px, 0);
  }
  50% {
    transform: rotate(10deg) translate(12px, 0);
  }
  100% {
    transform: rotate(-10deg) translate(-12px, 0);
  }
}

@keyframes totoro-guide-speech-animate {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
`);

export const loginGuideStyle = {
  ...style({
    position: 'absolute',
    'animation-iteration-count': 1,
    'animation-timing-function': 'cubic-bezier(0.26, 0.66, 0.67, 1.29)',
    left: '42px',
    top: 'calc(100% - 81px)',
  }, animate({name: 'totoro-guide-animate', duration: 0.7, repeat: false})),
  ...styleSelector(':hover svg', animate({name: 'totoro-guide-shake'})),
};

export const speechBubbleStyle = style({
  top: '-60px',
  left: '90px',
  width: '200px',
}, clickable, absolute, animate({
  name: 'totoro-guide-speech-animate', duration: 0.5, repeat: false, delay: 1,
}), opacity(0), px(3), py(2.5), background(WHITE), borderRadius, zIndex(1000));


