import {div, input} from 'render/html';
import {
  col, p, textRight, parentWidth, borderBottom,
} from 'style/atomic';
import style, {focusStyle} from 'style';

export const focused = {outline: 'none'};
export const inputStyle = {
  'background-color': 'transparent',
  border: 'none',
  ...borderBottom,
};

/**
 * Labeled form input
 * @param {Object<string,*>} attrs
 * @param {string} title
 * @param {Object<string,*>} inputAttrs
 */
export default function labeledInput(attrs, title, inputAttrs) {
  return (
    div({...style(p(3)), ...attrs}, [
      div(style(col(4), p(2), textRight), title),
      div(
        style(col(8), p(2)),
        input({
          ...style(inputStyle, parentWidth),
          ...focusStyle(focused),
          ...inputAttrs,
        }),
      ),
    ])
  );
}



