import {div} from 'render/html';
import style, {hoverStyle} from 'style';
import {
  topLeft, topRight, parentHeight, absolute, square, transitionColor,
  fill, visible, flexCenterContent, flexVertical, clickable,
} from 'style/atomic';
import {WHITE, GRAY} from 'style/colors';
import {chevron} from '../icons';

/**
 * Left/Right directional navigation buttons. Chevrons pointing in respective
 *   directions to allow user to navigate left/right.
 * @param {'left'|'right'} direction
 * @param {Function} onClick
 */
export default function sideDirectionalNavigation(direction, onClick) {
  const left = direction === 'left';
  return (
    div(
      style(left ? topLeft : topRight, parentHeight, absolute, flexCenterContent, flexVertical),
      chevron(direction, {
        onClick,
        ...style(fill(GRAY), visible(), square('5vw'), transitionColor, clickable),
        ...hoverStyle(fill(WHITE)),
      }),
    )
  );
}



