import {div, h1, form} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {
  parentWidth, p, textCenter, relative,
  borderRadius, overflowHidden, fontSize,
} from 'style/atomic';
import {LARGE} from 'style/fonts';
import style from 'style';
import centered from 'components/centered';
import blurred from 'components/blurred';
import labeledInput from 'components/labeledInput';
import actionButton from 'components/actionButton';
import {login, loadImages, showLoginGuide} from 'app/app.actions';
import {message} from 'i18n';

import loginGuide from './loginGuide.view';
import handleLoginFormSubmit from './handleLoginFormSubmit';
import showLoginGuideLater from './showLoginGuideLater';

/**
 * Login view container - blurred background with login form and login guide
 * @param {boolean} showLoginGuide - should show guide to help with login
 * @param {boolean} hideLoginGuideMessage
 */
function loginView(showLoginGuide, hideLoginGuideMessage) {
  showLoginGuideLater();
  return (
    centered(style({background: 'inherit'}), [
      showLoginGuide && loginGuide(hideLoginGuideMessage),
      blurred(
        style({width: '330px', height: '320px'}, borderRadius, overflowHidden),
        form({onSubmit: handleLoginFormSubmit, ...style(relative)}, [
          h1(style(textCenter), '360'),
          labeledInput({}, message('login.username')),
          labeledInput({}, message('login.password'), {type: 'password'}),
          div(
            style(p(3), parentWidth),
            actionButton({
              type: 'submit',
              onClick: handleLoginFormSubmit,
              ...style(parentWidth, fontSize(LARGE)),
            }, message('login.login')),
          ),
        ]),
      ),
    ])
  );
}

export default memoizeComponent(loginView);



