import {LIGHT_GRAY, MEDIUM_GRAY, WHITE} from './colors';

const unit = 4;
export const scale = p => (2 ** (p - 1)) * unit;
const pix = 'px';

export const clickable = {cursor: 'pointer'};

export const textCenter = {'text-align': 'center'};
export const textRight = {'text-align': 'right'};

const space = cls => size => ({[cls]: size === 0 ? 0 : scale(size) + pix});
export const m = space('margin');
export const mt = space('margin-top');
export const mb = space('margin-bottom');
export const ml = space('margin-left');
export const mr = space('margin-right');
export const mx = size => ({...ml(size), ...mr(size)});
export const my = size => ({...mt(size), ...mb(size)});

export const p = space('padding');
export const pt = space('padding-top');
export const pb = space('padding-bottom');
export const pl = space('padding-left');
export const pr = space('padding-right');
export const px = size => ({...pl(size), ...pr(size)});
export const py = size => ({...pt(size), ...pb(size)});

export const p3 = {padding: scale(3) + pix};

export const inlineBlock = {display: 'inline-block'};
export const displayNone = {display: 'none'};
export const visible = (isVisible = true) => ({visibility: isVisible ? 'visible' : 'hidden'});
export const opacity = op => ({opacity: op});
export const zIndex = index => ({'z-index': index});

export const transitionHeight = {transition: 'height 0.6s ease-out'};

export const topLeft = {top: 0, left: 0};
export const topRight = {top: 0, right: 0};
export const bottomLeft = {bottom: 0, left: 0};
export const fullView = {width: '100vw', height: '100vh'};
export const parentWidth = {width: '100%'};
export const parentHeight = {height: '100%'};
export const relative = {position: 'relative'};
export const absolute = {position: 'absolute'};
export const fixed = {position: 'fixed'};
export const fillContainer = {
  ...absolute, ...parentWidth, ...parentHeight, top: 0, left: 0,
};
export const square = size => ({width: size, height: size});

export const flexVertical = {display: 'flex', 'flex-direction': 'column'};
export const flexHorizontal = {display: 'flex', 'flex-direction': 'row'};
export const flexGrow = (amount = 1) => ({'flex-grow': amount});
export const flexShrink = (amount = 1) => ({'flex-shrink': amount});
export const flexSpaceBetween = {'justify-content': 'space-between'};
export const flexAlignItems = {'align-items': 'center'};
export const flexCenterContent = {...flexAlignItems, 'justify-content': 'center'};
export const alignMiddle = {'vertical-align': 'middle'};

export const animate = ({
  name, duration = 1, timingFunction = 'ease', delay = 0, repeat = true, direction = 'normal',
}) => ({
  animation: `${duration}s ${timingFunction} ${delay}s ${repeat ? 'infinite' : '1'} ${direction} ${repeat ? 'none' : 'forwards'}` +
  ` running ${name}`,
});
export const transitionColor = {transition: 'color 0.6s ease-out, fill 0.6s ease-out'};
export const fill = color => ({fill: color});
export const background = color => ({background: color});
export const textColor = color => ({color});
export const color = c => ({color: c, fill: c});
export const bgWhite = {background: WHITE};
export const textWhite = {color: WHITE};
export const bgMediumGray = {background: MEDIUM_GRAY};
export const textMediumGray = {color: MEDIUM_GRAY};
export const bgLightGray = {background: LIGHT_GRAY};
export const bgTransparentGray = {background: 'rgba(0,0,0,0.75)'};

export const fontSize = size => ({'font-size': size});
export const ellipsesText = {'white-space': 'nowrap', 'text-overflow': 'ellipsis', overflow: 'hidden'};
export const textUpper = {'text-transform': 'uppercase'};
export const underline = {'text-decoration': 'underline'};

const borderStyle = '1px solid #EFEFEF';
export const borderBottom = {'border-bottom': borderStyle};
export const borderTop = {'border-top': borderStyle};
export const borderY = {...borderTop, ...borderBottom};
export const overflowHidden = {overflow: 'hidden'};
export const scrollVertical = {'overflow-x': 'auto'};
export const scrollHorizontal = {'overflow-x': 'auto'};

export const backgroundImage = src => ({
  'background-image': `url(${src})`,
  'background-repeat': 'no-repeat',
  'background-attachment': 'fixed',
  'background-size': 'cover',
  'background-position-y': '50%',
});

export const textGradient = {
  background: 'linear-gradient(to bottom, transparent 0%, rgba(0,0,0,0.6) 100%)',
  ...color(WHITE),
};
export const textBackground = {background: 'rgba(0,0,0,0.6)', ...color(WHITE)};

export const boxShadow = {'box-shadow': '0px 3px 5px 5px rgba(0,0,0,0.26)'};

const brSize = 5 + pix;
export const borderRadiusBottomLeft = {'border-bottom-left-radius': brSize};
export const borderRadiusBottomRight = {'border-bottom-right-radius': brSize};
export const borderRadiusTopLeft = {'border-top-left-radius': brSize};
export const borderRadiusTopRight = {'border-top-right-radius': brSize};
export const borderRadius = {
  ...borderRadiusBottomLeft,
  ...borderRadiusBottomRight,
  ...borderRadiusTopLeft,
  ...borderRadiusTopRight,
};

export const col = share => ({width: `${(share / 12) * 100}%`, ...inlineBlock});



