import {login, loadImages} from 'app/app.actions';

/**
 * Handle form submit - begin login process
 * @param {Event} e
 */
export default function handleFormSubmit(e) {
  e.preventDefault();
  // proactively load images before transition to make images seem like they
  // are already loaded
  loadImages();

  // Hide jumbo wrapper header
  const jumbo = document.getElementById('jumbo-wrapper');
  const categoriesHeight = document.getElementById('categories-header').clientHeight;
  jumbo.style.height = `${categoriesHeight}px`;

  login();
}



