import {button} from 'render/html';
import {
  py, px, borderRadius, bgMediumGray, textWhite, clickable,
} from 'style/atomic';
import style, {styleSelector} from 'style';

const buttonStyle = {
  ...style({
    ...clickable,
    ...textWhite,
    ...px(2),
    ...py(3),
    border: 'none',
    ...bgMediumGray,
    ...borderRadius,
  }),
  ...styleSelector(':focus', {outline: 'none'}),
};

/**
 * Button used when taking a primary action. Use when creating, moving, or
 *   generally doing. Do not use when canceling, undoing, or moving tangentially
 *   from core workflow (ie getting additional information about something)
 * @param {Object<string,*>} attrs
 * @param {VirtualElement|VirtualElement[]|string|boolean} children
 */
export default function actionButton(attrs, children) {
  return (
    button(
      {
        type: 'button',
        ...buttonStyle,
        ...attrs,
      },
      children,
    )
  );
}



