import element from './element';

const namespace = 'http://www.w3.org/2000/svg';

export const svg = element('svg', namespace);
export const path = element('path', namespace);
export const rect = element('rect', namespace);
export const g = element('g', namespace);
export const line = element('line', namespace);
export const ellipse = element('ellipse', namespace);
export const title = element('title', namespace);



