import applyStyle from './applyStyle';

/**
 * Style based on size - creates media query based on range argument
 *   Range can be a single range, or a tuple of two ranges (low, high).
 *   if low or high are null, the low/high media query will be ignored
 *   and will have the effect of having an open range.
 *    eg:
 *      sizeStyle(MEDIUM, ...) => @media (min-width: MEDIUM.low px) and (max-width: MEDIUM.high px) {...}
 *      sizeStyle([LOW, MEDIUM], ...) => @media (min-width: LOW.low px) and (max-width: MEDIUM.high px) {...}
 *      sizeStyle([MEDIUM, null], ...) => @media (min-width: MEDIUM.low px) {...}
 *      sizeStyle([null, MEDIUM], ...) => @media (max-width: MEDIUM.high px) {...}
 * @param range
 * @param styles
 * @return {Object.<string, string>}
 */
export default function sizeStyle(range, ...styles) {
  let lowRange,
    highRange;
  if (Array.isArray(range) && range.filter(i => Array.isArray(i)).length) {
    [lowRange, highRange] = range;
  } else {
    lowRange = range;
    highRange = range;
  }
  const media = [];
  if (lowRange !== null) media.push(`(min-width : ${lowRange[0]}px)`);
  if (highRange !== null) media.push(`(max-width : ${highRange[1]}px)`);
  return applyStyle(media.join(' and '), '', ...styles);
}



