import uid from 'utility/uid';

import addRule from './addRule';
import objToString from './objToString';

const cache = new Map();

/**
 * Add style to DOM with media and selector. Parses styles and caches them
 *   so we don't add the same styles more than once
 * @param {string} media - media query for styles
 * @param {string} selector - selector for styles
 * @param {Object[]} styleObjs
 * @return {Object<string,string>} data-style attribute to add to element
 */
export default function applyStyle(media, selector, ...styleObjs) {
  let obj = {};
  styleObjs.forEach((s) => {
    if (obj) obj = ({...obj, ...s});
  });
  const asString = objToString(obj);
  const key = `${media}::${selector}::${asString}`;
  if (!cache.has(key)) {
    const styleId = uid('styled-');
    cache.set(key, styleId);
    const rule = `[data-${styleId}]${selector} { ${asString} }`;
    const css = media ? `@media ${media} { ${rule} }` : rule;
    addRule(css);
  }
  return {[`data-${cache.get(key)}`]: ''};
}



