import {showLoginGuide} from 'app/app.actions';
import once from 'utility/once';
import sleep from 'utility/sleep';

/**
 * Show login guide 3 seconds after login page first renders
 */
const showLoginGuideLater = once(() => {
  sleep(3000)
    .then(showLoginGuide);
});

export default showLoginGuideLater;



