import {div} from 'render/html';
import {
  fullView, flexVertical, flexShrink, transitionHeight,
} from 'style/atomic';
import style from 'style';
import jumboView from 'login/jumbo.view';
import categoriesView from 'categories/categories.view';
import imagesView from 'images/imageView.view';

import './base.style';

/**
 * @param {AppStore} data
 */
export default function appView(data) {
  return (
    div(style(fullView, flexVertical), [
      div({...style(transitionHeight, fullView, flexVertical, flexShrink(0)), id: 'jumbo-wrapper'}, [
        jumboView(data.showLoginGuide, data.hideLoginGuideMessage),
        categoriesView(
          data.currentCategory,
          data.photoCategories,
          data.isLoggedIn,
          data.isSearchMode,
        ),
      ]),
      data.isLoggedIn && imagesView(
        data.images,
        data.currentFullScreenImage,
        data.isLoading,
        data.totalResults,
        data.getQueryTerm(),
        data.loadErrorMessage,
      ),
    ])
  );
}



