import element from './element';

const namespace = document.documentElement.namespaceURI;

export const div = element('div', namespace);
export const span = element('span', namespace);
export const b = element('b', namespace);
export const h1 = element('h1', namespace);
export const h2 = element('h2', namespace);
export const h3 = element('h3', namespace);
export const h4 = element('h4', namespace);
export const img = element('img', namespace);
export const input = element('input', namespace);
export const button = element('button', namespace);
export const form = element('form', namespace);



