import {
  px, pt, pb, inlineBlock, relative, displayNone, clickable,
  parentWidth, ml, m, scale,
} from 'style/atomic';
import style, {styleSelector, sizeStyle} from 'style';
import {MOBILE, SMALL, MEDIUM, LARGE} from 'style/sizes';
import sum from 'utility/sum';

export const wrapperStyle = index => ({
  ...sizeStyle([LARGE, null], px(6)),
  ...sizeStyle(MEDIUM, px(4)),
  ...sizeStyle(SMALL, px(2)),
  ...sizeStyle([SMALL, null], index === 0 && pt(3), pb(2)),
  ...styleSelector(' > div', {opacity: '0'}),
  ...styleSelector('[data-children-loaded] > div', {opacity: '1'}),
});

export const imageStyle = style(
  inlineBlock,
  clickable,
  parentWidth,
  {'vertical-align': 'middle'},
);

export const container = (width, index) => ({
  ...style(
    {transition: 'opacity 3s'},
    relative,
  ),
  ...sizeStyle(MOBILE, m(2)),
  ...sizeStyle([SMALL, null], index !== 0 && ml(2), inlineBlock, {width, 'transition-delay': `${index / 3}s`}),
  ...styleSelector(' > .details', displayNone),
  ...styleSelector(':hover > .details', inlineBlock),
});

export const getWidthGetter = images => {
  const padding = scale(2);
  const unitWidths = images.map(imgModel => imgModel.width / imgModel.height);
  const rowSumUnitWidth = sum(unitWidths);
  return index => {
    const unitWidth = unitWidths[index];
    const cellPadding = unitWidth => (padding * (images.length - 1) * unitWidth) / rowSumUnitWidth;
    return `calc(${(100 * (unitWidth / rowSumUnitWidth))}% - ${cellPadding(unitWidth)}px)`;
  }
};



