import {div} from 'render/html';
import memoizeComponent from 'render/memoizeComponent';
import {
  parentWidth, flexGrow, backgroundImage, relative, overflowHidden,
} from 'style/atomic';
import style from 'style';
import {choice} from 'utility/random';
import loginView from './login.view';

const image = choice([
  'https://static.pexels.com/photos/239659/pexels-photo-239659.jpeg',
  'https://c.pxhere.com/photos/7e/97/beautiful_the_leaves_flowers_red_green_leaf-890689.jpg!d',
  'https://static.pexels.com/photos/273941/pexels-photo-273941.jpeg',
  'https://static.pexels.com/photos/268533/pexels-photo-268533.jpeg',
  'https://static.pexels.com/photos/35537/child-children-girl-happy.jpg',
  'https://static.pexels.com/photos/2242/wall-sport-green-bike.jpg',
]);

/**
 * Jumbo header view - displayed when user isn't logged in
 * @param {boolean} showLoginGuide - should show guide to help with login
 * @param {boolean} hideLoginGuideMessage
 */
function jumboView(showLoginGuide, hideLoginGuideMessage) {
  return (
    div(
      style(
        relative,
        parentWidth,
        overflowHidden,
        flexGrow(),
        backgroundImage(image),
      ),
      loginView(showLoginGuide, hideLoginGuideMessage),
    )
  );
}

export default memoizeComponent(jumboView);



