import {dispatch, getState} from 'data-store';
import {message} from 'i18n';

export const LOGIN = 'LOGIN';
export const login = () => dispatch(LOGIN);

export const HIDE_LOGIN_GUIDE_MESSAGE = 'HIDE_LOGIN_GUIDE_MESSAGE';
export const hideLoginGuideMessage = () => dispatch(HIDE_LOGIN_GUIDE_MESSAGE);

export const SHOW_LOGIN_GUIDE = 'SHOW_LOGIN_GUIDE';
export const showLoginGuide = () => dispatch(SHOW_LOGIN_GUIDE);

export const SELECT_CATEGORY = 'SELECT_CATEGORY';
export const selectCategory = category => dispatch(SELECT_CATEGORY, category);

export const IMAGES_LOADED = 'IMAGES_LOADED';
export const IMAGES_LOAD_FAILED = 'IMAGES_LOAD_FAILED';
export const IMAGE_LOAD_STARTED = 'IMAGE_LOAD_STARTED';
export const loadImages = () => {
  if (getState().shouldLoadImages()) {
    dispatch(IMAGE_LOAD_STARTED);
    getState().loadImages()
      .then(([count, images]) => dispatch(IMAGES_LOADED, count, images))
      .catch(e => {
        let msg;
        const {status} = e.target;
        if (status === 0) {
          msg = message('images.status.connectivity');
        } else {
          msg = message('images.status.failed');
        }
        dispatch(IMAGES_LOAD_FAILED, msg);
      });
  }
};

export const SET_CURRENT_FULLSCREEN_IMAGE = 'SET_CURRENT_FULLSCREEN_IMAGE';
export const setFullScreenImage = imageIndex => dispatch(SET_CURRENT_FULLSCREEN_IMAGE, imageIndex);

export const GO_TO_NEXT_FULLSCREEN_IMAGE = 'GO_TO_NEXT_FULLSCREEN_IMAGE';
export const goToNextFullScreenImage = () => dispatch(GO_TO_NEXT_FULLSCREEN_IMAGE);

export const GO_TO_PREVIOUS_FULLSCREEN_IMAGE = 'GO_TO_PREVIOUS_FULLSCREEN_IMAGE';
export const goToPreviousFullScreenImage = () => dispatch(GO_TO_PREVIOUS_FULLSCREEN_IMAGE);

export const CLOSE_FULL_SCREEN_IMAGE = 'CLOSE_FULL_SCREEN_IMAGE';
export const closeFullScreenImage = () => dispatch(CLOSE_FULL_SCREEN_IMAGE);

export const ENTER_SEARCH_MODE = 'ENTER_SEARCH_MODE';
export const enterSearchMode = () => dispatch(ENTER_SEARCH_MODE);

export const EXIT_SEARCH_MODE = 'EXIT_SEARCH_MODE';
export const exitSearchMode = () => dispatch(EXIT_SEARCH_MODE);

export const UPDATE_SEARCH_TERM = 'UPDATE_SEARCH_TERM';
export const updateSearchTerm = term => dispatch(UPDATE_SEARCH_TERM, term);



