import ImmutableModel from 'data-store/ImmutableModel';
import each from 'utility/each';

export default class Image extends ImmutableModel {
  /** @return {string} */
  get id() { return this.get('id'); }
  /** @return {string} */
  get title() { return this.get('title'); }
  /** @return {string} */
  get previewUrl() { return this.get('previewUrl'); }
  /** @return {string} */
  get largeUrl() { return this.get('largeUrl'); }
  /** @return {number} */
  get width() { return this.get('width'); }
  /** @return {number} */
  get height() { return this.get('height'); }
  constructor(id, title, previewUrl, largeUrl, width, height) {
    super();
    return this
      .set('id', id)
      .set('title', title)
      .set('previewUrl', previewUrl)
      .set('largeUrl', largeUrl)
      .set('width', width)
      .set('height', height);
  }

  /**
   * Process images from Getty API - http://developers.gettyimages.com/docs/#tag/Search
   * @param {Object} data - getty image result json data
   * @return {Image}
   */
  static fromGettyBackend(data) {
    const {id, title} = data;
    const {width, height} = data.max_dimensions;
    let previewUrl;
    let largeUrl;
    each(data.display_sizes, size => {
      if (size.name === 'comp') largeUrl = size.uri;
      if (size.name === 'preview') previewUrl = size.uri;
    });
    return new Image(id, title, previewUrl, largeUrl, width, height);
  }
}



