import addRule from './addRule';
import applyStyle from './applyStyle';
import sizeStyle from './sizeStyle';

export const style = (...styles) => applyStyle('', '', ...styles);
export default style;
export const styleSelector = (selector, ...styles) => applyStyle('', selector, ...styles);
export const hoverStyle = (...styles) => applyStyle('', ':hover', ...styles);
export const focusStyle = (...styles) => applyStyle('', ':focus', ...styles);
export const styleMedia = (media, ...styles) => applyStyle(media, '', ...styles);

export {
  applyStyle,
  addRule,
  sizeStyle,
};



